@import "relative";

$primary: #34547A;
$silver: #EEEFF1;

body {
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
}

a {
  text-decoration: none;
  display: inline-block;
}

img {
  max-width: 100%;
}

.container {
  width: 95%;
  margin: 0 auto;
}

.title {
  font-weight: 500;
  line-height: 79px;
  color: #000000;
  font-size: 1.5rem;
}

.text {
  line-height: 26px;
  color: #727272;
}

.btn {
  border: none;
  background: $primary;
  color: #FFFFFF;
  padding: 25px 41px;
  transition: all .3s ease-in-out;
  &:hover {
    background: white;
    color: $primary;
  }
}

header {
  background: $silver;
  padding: 57px 0 146px 0;
  height: 100vh;
}

.header {
  &__navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    & > ul {
      display: flex;
      & > li {
        list-style: none;
        font-weight: 500;
        letter-spacing: 0.1em;
        & > a {
          padding-left: 42px;
          color: #000000;
          transition: color .1s ease-in-out;
          &:hover {
            color: $primary;
          }
          &.active {
            color: $primary;
          }
        }
      }
    }
  }

  &__brand {
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
  }

  &__content {
    margin-top: 162px;
    display: flex;
    & > img {
      height: 357px;
      margin-left: 96px;
    }
  }

  &__description {
    margin-left: 131px;
  }

  &__title {
    font-size: 2rem;
  }

  &__text {
    margin-bottom: 51px;
  }
}

#about {
  padding: 119px 0 100px 0;
  width: 540px;
  margin: 0 auto;
  text-align: center;
}

#service {
  background: #34547A;
  padding: 103px 0;
}

.service {
  &-wrap {
    display: flex;
  }

  &__item {
    display: flex;
    color: #FFFFFF;
    width: calc(100% / 6);
  }

  &__info {
    margin-left: 20px;
  }

  &__title {
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 25px;
    color: #FFFFFF;
  }
}

#my-skills {
  padding: 102px 0;
}

.skills {
  &__skill {
    position: relative;
    margin-top: 54px;
    color: #727272;
    &::before {
      content: "";
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 445px;
      height: 4px;
      background: linear-gradient(to right, $primary 80%, #C4C4C4 50%);
    }
  }
}

.my-skills-wrap {
  display: flex;
  justify-content: space-between;
  & > img {
    width: 300px;
  }
}

#how-i-work {
  background: $silver;
  text-align: center;
  padding: 100px 0 128px 0;
}

.how-i-work__text {
  width: 540px;
  margin: 0 auto;
  margin-bottom: 57px;
}

#portfolio {
  width: 100%;
  margin: 0;
  .portfolio__row {
    display: flex;
  }
  .portfolio__item {
    width: 25%;
    display: flex;
    position: relative;
    transition: all 2s;
    &::before {
      left: -100%;
      transition: all 2s;
    }
    & > .portfolio__overlay {
      display: flex;
      justify-content: center;
      align-items: center;
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: .5s ease-out;
      top: 0;
      left: 0;
      background: rgba(52, 84, 122, 0.8);
      &:hover {
        opacity: 1;
      }
    }
  }
}

#cooperation {
  text-align: center;
  padding: 72px 0 100px 0;
}

#contact-form {
  background: $silver;
  padding: 100px 0;
}

.contact-form {
  &_wrap {
    width: 540px;
    margin: 0 auto;
  }
  &__info {
    text-align: center;
  }
  &__group {
    margin: 20px 0;
    display: flex;
    & > input, & > textarea {
      border: none;
      outline: none;
      padding: 17px 20px;
      width: 100%;
    }
  }
  &__name, &__email {
    width: 50% !important;
  }
  &__email {
    margin-left: 30px;
  }
  &__message {
    padding-bottom: 143px !important;
  }
  &__btn {
    display: flex;
    justify-content: center;
  }
}

footer {
  background: $primary;
  padding: 50px 0;
}

.footer {
  &_wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title, &__text {
    color: white;
  }
  &__title {
    line-height: 25px;
  }
  &__text {
    font-size: .9rem;
  }
}
